import api from '@/api/index'
import * as types from '@/store/mutations'

const state = {
  transaction: {},
  codebase64: '' // 验证码 base64 格式
}

const fn = function () {}

const actions = {
  getTransaction ({commit, state}, {cb = fn, params}) {
    return api.getTransaction(params)
    .then((data) => {
      commit(types.TRANSACTION, data)
      cb(data)
    })
    .catch(e => {
      cb(e)
    })
  },
  getCodebase64 ({ commit, state }, {cb = fn}) {
    return api.getVerifyCode()
    .then((data) => {
      commit(types.CODEBASE64, data)
      cb(data)
    })
    .catch(e => {
      cb(e)
    })
  },
  getValidateCode ({ commit, state }, { cb = fn, params }) {
    return api.getValidateCode(params)
      .then((data) => {
        cb(data)
      })
      .catch(e => {
        cb(e)
      })
  },
  setForgetpassword ({ commit, state }, { cb = fn, params }) {
    return api.setForgetpassword(params)
    .then((data) => {
      cb(data)
    })
    .catch(e => {
      cb(e)
    })
  },
  getSendemail ({ commit, state }, { cb = fn, params }) {
    return api.getSendemail(params)
      .then((data) => {
        cb(data)
      })
      .catch(e => {
        cb(e)
      })
  }
}

export const TRANSACTION = 'TRANSACTION'

const mutations = {
  [types.TRANSACTION] (states, data) {
    states.transaction = data
  },
  [types.CODEBASE64] (states, data) {
    states.codebase64 = data
  }
}

export default {
  state,
  actions,
  mutations
}



