const state = {
  user: JSON.parse(window.sessionStorage.getItem('user')) || {}, // 用户对象, 只要用户登录就有用户信息，memberId, token, nickName, userType
  userSignin: {}, // 用户登录后可让 userSignin 为 true
  userSignout: {}, // 用户退出可标记下 userSignout
  loadingbar: 0, // loadingbar，0表示结束。1表示开始
  userName: '', // 用户登录邮箱
  memberId: '', // 用户 id
  nickName: '', // 用户名
  token: '', // token
  balance: 0, // 余额
  roleType: 0, // 0 是普通用户，1是管理员（前期可能没有角色，也没有管理员）
  english: false, // 英文
  pets: [], // 市场宠物信息
  mypets: [], // 我的宠物信息
  pet: {}, // 当前某个宠物信息
  agreement: {}, // 抽奖协议
  notice: '', // 公告滚动内容字符串
  petAttribute: {}, // 宠物属性
  currentPath: '', // 当前地址
  lotteryNum: 0 // 抽奖后计算用户查询了几次
}

export default state



