import * as types from './mutation-types'

import api from '@/api/index'

import states from './state.js'

const fn = function () {}

let userToken = states.user.token

const actions = {
  setUser ({ commit }, user) {
    commit(types.USER, user)
  },
  setUserSignin ({ commit }, user) {
    commit(types.USER_SIGNIN, user)
  },
  setUserSignout ({ commit }) {
    commit(types.USER_SIGNOUT)
  },
  setLotteryNum ({ commit }, data) {
    commit(types.LOTTERY_NUM, data)
  },
  setLoadingbar ({ commit }, loadingbar) {
    commit(types.LOADINGBAR, loadingbar)
  },
  setMemberId ({ commit }, memberId) {
    commit(types.MEMBER_ID, memberId)
  },
  setNickName ({ commit }, nickName) {
    commit(types.NICK_NAME, nickName)
  },
  setToken ({ commit }, token) {
    commit(types.TOKEN, token)
  },
  setBalance ({ commit }, balance) {
    commit(types.BALANCE, balance)
  },
  setRoleType ({ commit }, roleType) {
    commit(types.ROLE_TYPE, roleType)
  },
  setLogin ({ commit }, { cb = fn, userName, passWord }) {
    return api.setLogin({userName, passWord})
    .then((data) => {
      commit(types.USER, data)
      commit(types.USER_NAME, data.userName)
      commit(types.USER_SIGNIN, data)
      cb(null, data)
    })
    .catch()
  },
  setEnglish ({ commit }, english) {
    commit(types.ENGLISH, english)
  },
  setCurrentPath ({ commit }, currentPath) {
    commit(types.CURRENT_PATH, currentPath)
  },
  getPets ({ commit }, { cb = fn, params }) {
    if (userToken) {
      api.init({
        token: userToken
      })
    }
    return api.getPets(params)
    .then((data) => {
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  getPetAttribute ({ commit }, { cb = fn, params }) {
    if (userToken) {
      api.init({
        token: userToken
      })
    }
    return api.getPetAttribute(params)
      .then((data) => {
        cb(null, data)
      })
      .catch(e => {
        cb(e)
      })
  },
  setPet ({ commit }, pet) {
    commit(types.PET, pet)
  },
  getAgreement ({ commit }, { cb = fn }) {
    return api.getAgreement()
    .then((data) => {
      commit(types.AGREEMENT, data)
      cb(null, data)
    })
    .catch((e) => {
      commit(types.AGREEMENT, e)
      cb(null, e)
    })
  },
  getNotice ({ commit }, { cb = fn }) {
    return api.getNotice()
      .then((data) => {
        commit(types.NOTICE, data.text)
        cb(null, data)
      })
      .catch((e) => {
        commit(types.NOTICE, e.text)
        cb(null, e)
      })
  },
  getCapture ({ commit }, { cb = fn, params}) {
    return api.getCapture(params)
      .then((data) => {
        cb(null, data)
      })
      .catch((e) => {
        cb(null, e)
      })
  }
}

export default actions



