import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', // 欢迎页面, 欢迎页面目前没有写，之前是做初始跳转用的
      name: 'Welcome',
      meta: { auth: false },
      component: resolve => require(['@/pages/welcome'], resolve)
    },
    {
      path: '/login', // 登录页面 最开始展示的页面
      name: 'Login',
      meta: { auth: false },
      component: resolve => require(['@/pages/login'], resolve)
    },
    {
      path: '/home', // 主页, 经过登录页面后是主页
      name: 'Home',
      meta: { auth: true },
      component: resolve => require(['@/pages/home'], resolve)
    },
    {
      path: '/terms', // 注册协议页面
      name: 'Terms',
      meta: { auth: false },
      component: resolve => require(['@/pages/terms'], resolve)
    },
    {
      path: '/lottery', // 抽奖页面
      name: 'Lottery',
      meta: { auth: true },
      component: resolve => require(['@/pages/lottery'], resolve)
    },
    {
      path: '/agreement', // 抽奖的协议页面
      name: 'Agreement',
      meta: { auth: true },
      component: resolve => require(['@/pages/agreement'], resolve)
    },
    {
      path: '/mine', // 我的 个人中心
      name: 'Mine',
      meta: { auth: true },
      component: resolve => require(['@/pages/home'], resolve)
    },
    {
      path: '/personal', // 个人信息 页面
      name: 'Personal',
      meta: { auth: true },
      component: resolve => require(['@/pages/personal'], resolve)
    },
    {
      path: '/help', // 帮助中心 页面
      name: 'Help',
      meta: { auth: true },
      component: resolve => require(['@/pages/help'], resolve)
    },
    {
      path: '/phone', // 帮助中心 页面
      name: 'phone',
      meta: { auth: true },
      component: resolve => require(['@/pages/phone'], resolve)
    },
    {
      path: '/recharge', // 充值中心 页面
      name: 'recharge',
      meta: { auth: true },
      component: resolve => require(['@/pages/recharge'], resolve)
    },
    {
      path: '/pet', // 宠物详情页面
      name: 'Pet',
      meta: { auth: true },
      component: resolve => require(['@/pages/pet'], resolve)
    },
    {
      path: '/market', // 市场 页面
      name: 'Market',
      meta: { auth: true },
      component: resolve => require(['@/pages/home'], resolve)
    },
    {
      path: '/mountain', // 熊猫村 页面
      name: 'Mountain',
      meta: { auth: true },
      component: resolve => require(['@/pages/home'], resolve)
    },
    {
      path: '/404', // 404
      component: resolve => require(['@/pages/404'], resolve)
    },
    {
      path: '*', // 其他页面，强制跳转到登录页面
      redirect: '/login'
    }
  ]
})



