import Vue from 'vue'
import { sync } from 'vuex-router-sync'
import App from './App'
import router from './router'
import store from './store/index.js'

/* 使用 axios */
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

// 使用 mint-ui
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(Mint)

// 使用 vant
// import Vant from 'vant'
// import 'vant/lib/vant-css/index.css'
// Vue.use(Vant)

// 引用全局样式文件
import './assets/scss/tsxm.scss'

// 使用上传头像组件
import AvatarUpload from './components/avatar_upload'
Vue.use(AvatarUpload)

sync(store, router)

axios.defaults.baseURL = '/v1/tsxm' // axios 添加api上下文
// axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded' // form表单数据被编码为key/value格式发送到服务器（表单默认的提交数据的格式）
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// 在每次路由跳转时都对 meta 和 token 进行验证，看是否有权限
router.beforeEach(({meta, path}, from, next) => {
  console.log('meta, path ----> ', meta, path)
  console.log('from---->', from)
  console.log('next--->', next)
  if (meta.auth) { // 如果 meta.auth 为 true
    if (store.state.user.token) { // 通过 vuex state 获取当前的 token 是否存在
      // 如果有 token 那么跳到下一路由
      next()
    }
    else { // token 不存在
      // meta.auth 为true，但 token 不存在
      return next({ path: '/login' })
    }
  }
  else { // 如果 meta.auth 为 false, 除了那些不需要 meta.auth 为true 的页面，其他一律跳到登录页面
    if (path !== '/login' && path !== '/agreement') {
      return next({ path: '/login' })
    }
    else {
      next()
    }
  }
})

// 路由跳转之后
router.afterEach(function ({ to, from }) {
  // console.log('afterEach')
  // console.log('to, from --->', to, from)
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



