import Vue from 'vue'
import * as types from './mutation-types'

const mutations = {
  [types.USER] (states, data) {
    states.user = data
  },
  [types.USER_NAME] (states, data) {
    states.userName = data
  },
  [types.USER_SIGNIN] (states, data) {
    sessionStorage.setItem('user', JSON.stringify(data))
    states.userSignin = data
  },
  [types.USER_SIGNOUT] (states) {
    sessionStorage.removeItem('user')
    Object.keys(states).forEach(k => Vue.delete(states, k))
  },
  [types.LOADINGBAR] (states, data) {
    states.loadingbar = data
  },
  [types.MEMBER_ID] (states, data) {
    states.memberId = data
  },
  [types.NICK_NAME] (states, data) {
    states.nickName = data
  },
  [types.TOKEN] (states, data) {
    states.token = data
  },
  [types.BALANCE] (states, data) {
    states.balance = data
  },
  [types.ROLE_TYPE] (states, data) {
    states.roleType = data
  },
  [types.ENGLISH] (states, data) {
    states.english = data
  },
  [types.PETS] (states, data) {
    states.pets = data
  },
  [types.MYPETS] (states, data) {
    states.mypets = data
  },
  [types.PET] (states, data) {
    states.pet = data
  },
  [types.AGREEMENT] (states, data) {
    states.agreement = data
  },
  [types.NOTICE] (states, data) {
    states.notice = data
  },
  [types.ATTRIBUTE] (states, data) {
    states.attribute = data
  },
  [types.CURRENT_PATH] (states, data) {
    states.currentPath = data
  },
  [types.LOTTERY_NUM] (states, data) {
    states.lotteryNum = data
  }
}

export default mutations



