import { init, axiosFactory, axiosPostFactory} from './axios-factory'

// 生成验证码图片
function getVerifyCode () {
  return axiosFactory({
    method: 'get',
    url: 'genverifycode'
  })
}

// 验证验证码是否正确
function getValidateCode (params) {
  return axiosFactory({
    method: 'get',
    url: 'validateCode',
    params
  })
}

// 发送邮箱验证码
function getSendemail (params) {
  return axiosFactory({
    method: 'get',
    url: 'sendemail',
    params
  })
}

// 验证邮箱验证码
function getValidemail (params) {
  return axiosFactory({
    method: 'get',
    url: 'validemail',
    params
  })
}

// 获取熊猫数据
function getPets (params) {
  return axiosFactory({
    method: 'get',
    url: 'getPets',
    params
  })
}

// 获取熊猫属性
function getPetAttribute (params) {
  return axiosFactory({
    method: 'get',
    url: 'getPetAttribute',
    params
  })
}

// 捕获
function getCapture (params) {
  return axiosFactory({
    method: 'get',
    url: 'capture',
    params
  })
}

// 训练
function getTrain (params) {
  return axiosFactory({
    method: 'get',
    url: 'train',
    params
  })
}

// 协议信息
function getAgreement () {
  return axiosFactory({
    method: 'get',
    url: 'agreement'
  })
}

// 获取公告滚动内容
function getNotice () {
  return axiosFactory({
    method: 'get',
    url: 'notice'
  })
}

// 获取余额
function getBalance (params) {
  return axiosFactory({
    method: 'get',
    url: 'balance',
    params
  })
}

// 查询交易记录
function getTransaction (params) {
  return axiosFactory({
    method: 'get',
    url: 'transaction',
    params
  })
}

// 验证码
function getVerifycode (data) {
  return axiosPostFactory({
    url: 'verifycode',
    data
  })
}

// 注册
function setRegist (data) {
  return axiosPostFactory({
    url: 'regist',
    data
  })
}

// 登录
function setLogin (data) {
  return axiosPostFactory({
    url: 'login',
    data
  })
}

// 忘记密码
function setForgetpassword (data) {
  return axiosPostFactory({
    url: 'forgetpassword',
    data
  })
}

// 出售
function setSell (data) {
  return axiosPostFactory({
    url: 'sell',
    data
  })
}

// 充值
function setRecharge (data) {
  return axiosPostFactory({
    url: 'recharge',
    data
  })
}

// 修改昵称
function setModifyname (data) {
  return axiosPostFactory({
    url: 'modifyname',
    data
  })
}

let api = {
  init,
  getVerifyCode,
  getValidateCode,
  getSendemail,
  getValidemail,
  getPets,
  getPetAttribute,
  getCapture,
  getTrain,
  getAgreement,
  getNotice,
  getBalance,
  getTransaction,
  getVerifycode,
  setRegist,
  setLogin,
  setForgetpassword,
  setSell,
  setRecharge,
  setModifyname
}

export default Object.assign({}, api)



