import qs from 'qs'
let axios = window.axios
axios.default.baseURL = '/v1/tsxm'

// 对 request 规范化, 拦截器
axios.interceptors.request.use((request) => {
  // do something before request is sent
  if (request.data && request.headers['Content-Type'] === 'application/x-www-form-urlencoded') {
    request.data = qs.stringify(request.data, { allowDots: true })
  }
  return request
})

// 统一添加 token
export function init ({ token = '' }) {
  axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
}

// axios 工厂函数
// method, url, params, data
export function axiosFactory ({ method, url, params, data }) {
  return new Promise((resolve, reject) => {
    axios({
      method,
      url,
      baseURL: '/v1/tsxm',
      params, // 不能再这里直接设置headers，会覆盖Authorization
      data
    })
      .then(({ data }) => {
        data.success ? resolve(data.data) : reject(data)
      }, (err) => {
        reject(err)
      }).catch((err) => {
        reject(err)
      })
  })
}

// axios post 工厂函数
// url, data
export function axiosPostFactory ({ url, data }) {
  return new Promise((resolve, reject) => {
    axios({
      method: 'post',
      url,
      baseURL: '/v1/tsxm',
      data,
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
    })
      .then(({ data }) => {
        if (data.success) {
          if (data.data) {
            if (data.data.msg) {
              data.data.msg = data.msg
            }
            resolve(data.data)
          }
          else {
            resolve(data) // post 可能只有成功，没有消息
          }
        }
        else {
          reject(data)
        }
      })
      .catch((error) => {
        reject(error)
      })
  })
}



